.loginForm {
  max-width: 320px;
  background-color: #ffc94b;
  border: 1px solid #ffc94b;
  border-radius: 6px;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  padding: 30px;
  display: flex;
}

.loginForm label {
  color: #4a6163;
  margin-bottom: 16px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 600;
}

.loginForm input, .loginForm button {
  width: 100%;
  font: inherit;
  color: #4a6163;
  border: 1px solid #f9a66c;
  border-radius: 6px;
  padding: 4px;
  font-weight: 500;
}

.loginForm input:focus {
  border: 2px solid #f9a66c;
  outline: none;
}

.loginForm button {
  color: #fff;
  background-color: #f9a66c;
  font-weight: 600;
}

.loginForm button:hover {
  background-color: #f17a7e;
}

